import React, { useState } from 'react'
import { Carousel, Row, Col, Modal } from 'antd';
//import AdSense from 'react-adsense';
import Intl from 'react-intl-universal';
function DetailContent() {
    const [Adstate, setAdstate] = useState(true);
    const handleCancel = () => {
        setAdstate(false);
    };
    return (
        <div>
            {Adstate === true ?
                <div>
                    <Modal title={Intl.get('AD')}
                        open={Adstate}
                        onCancel={handleCancel}
                        width={1000}
                        okButtonProps={{ display: false }}
                        footer={null}
                    >
                        <p>a</p><p>b</p><p>c</p><p>d</p><p>e</p><p>f</p>
                        {/* <AdSense.Google client="ca-pub-6251011828646312"
                            slot="6754370832"
                            style={{ width: 500, height: 300, float: 'left' }}
                            format='' /> */}
                    </Modal>
                </div>
                : (
                    <div id='mainbox'>
                        <Row gutter={[16, 16]}>
                            <Col span={8}>
                                <Carousel>
                                    <div><img src="https://picsum.photos/id/157/200/200" alt="" /></div>
                                    <div><img src="https://picsum.photos/id/158/200/200" alt="" /></div>
                                    <div><img src="https://picsum.photos/id/258/200/200" alt="" /></div>
                                    <div><img src="https://picsum.photos/id/217/200/200" alt="" /></div>
                                </Carousel>
                            </Col>
                            <Col span={12}>
                                <p>{Intl.get('WEB3')}</p>
                            </Col>
                        </Row>
                    </div >
                )
            }
        </div>

    )
}

export default DetailContent